import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { InputTextModule } from 'primeng/inputtext';
import { InputNumberModule } from 'primeng/inputnumber';
import { ButtonModule } from 'primeng/button';
import { ImageModule } from 'primeng/image';
import { MessageService } from 'primeng/api';
import { ToastModule } from 'primeng/toast';
import { ApiService } from '../../services/api.service';
import { PlayerDTO } from '../../dto/playerDTO';

@Component({
  selector: 'app-player-management',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    InputTextModule,
    InputNumberModule,
    ButtonModule,
    ImageModule,
    ToastModule
  ],
  templateUrl: './player-management.component.html',
  styleUrl: './player-management.component.scss'
})
export class PlayerManagementComponent implements OnInit {
  player: PlayerDTO = {
    name: '',
    firstname: '',
    age: 0,
    email: '',
    pseudo: '',
    picture: ''
  };

  availableImages: string[] = [];
  loading: boolean = false;

  constructor(
    private apiService: ApiService,
    private messageService: MessageService
  ) { }

  ngOnInit() {
    this.loadImages();
  }

  loadImages() {
    this.apiService.get<string[]>('personnages').subscribe(images => {
      if (images) {
        this.availableImages = images;
      }
    });
  }

  selectImage(image: string) {
    this.player.picture = image;
  }

  onSubmit() {
    if (!this.isValid()) {
      this.messageService.add({
        severity: 'warn',
        summary: 'Attention',
        detail: 'Veuillez remplir tous les champs obligatoires et choisir une image.'
      });
      return;
    }

    this.loading = true;
    this.apiService.post('players', this.player).subscribe({
      next: () => {
        this.messageService.add({
          severity: 'success',
          summary: 'Succès',
          detail: 'Joueur créé avec succès !'
        });
        this.resetForm();
        this.loading = false;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  isValid(): boolean {
    return !!(
      this.player.name &&
      this.player.firstname &&
      this.player.email &&
      this.player.pseudo &&
      this.player.picture
    );
  }

  resetForm() {
    this.player = {
      name: '',
      firstname: '',
      age: 0,
      email: '',
      pseudo: '',
      picture: ''
    };
  }
}
